
.main {
  min-height: 100vh;
  display: grid;
  grid-template-columns: minmax(250px, 20%) 1fr;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: auto 100%;
  background-attachment: fixed;
  z-index: 0;
}

/* !! menu !! */
@media (max-width: 1000px) {
  .main {
    grid-template-columns: 1fr;
    grid-template-rows: 50px 1fr;
  }
}


.elseBack {
  background-color: rgb(219, 219, 219);
}

.clear {
  background-image: url('../images/clear.jpg');
}

.cloudy {
  background-image: url('../images/cloudy.jpg');
}

.rain {
  background-image: url('../images/rainB.jpg');
}

.snow {
  background-image: url('../images/snow.jpg');
}

.thunderstorm {
  background-image: url('../images/thunderstorm.jpg');
}

/*------sidePanel------------------------*/
.menu {
  width: 100%;
  min-height: 100%;
  display: grid;
  align-content: space-around;
  height: 100vh;
  grid-template-columns: 1fr;
  background-color: rgba(0, 0, 0, 0.61);
  backdrop-filter: blur(8px);
  z-index: 1;
  transition: left 1s;
}

@media (max-width: 1000px) {
  .menu {
    width: 100%;
    height: 68px;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    align-content: end;
  }
}

.headerMenu {
  height: fit-content;
  width: fit-content;
  max-width: 70%;

  justify-self: center;
}
.headerMenu h2 {
  display: flex;
  justify-content: center;
  margin-bottom: 0;
}
.headerMenu hr {
  border: 1px solid rgb(185, 185, 185);
}

.headerMenu h3 {
  display: flex;
  justify-content: center;
  margin-top: 0;
  color: rgb(221, 221, 221);
}



@media (max-width: 1000px) {

  h2 {
    margin: 0;
    padding-bottom: 0;
  }

  h2 img {
    width: 48px;
  }
}

.logo {
  color: white;
  font-size: 38px;
}

.buttonsMenu {
  width: 100%;
  height: 350px;
  justify-self: end;
  justify-items: end;
  display: grid;
  grid-template-columns: 1fr;
  font-size: 23px;
}

.linkActive {
  width: fit-content;
  height: fit-content;
  padding: 20px 30px;
  text-decoration: none;

  background-color: rgba(0, 0, 0, 0);
  color: white;
  border: none;
  padding-right: 40%;
  border-right: 4px solid rgb(255, 255, 255);
  transition: 0.2s all;

}

.linkNotActive {
  width: fit-content;
  height: fit-content;
  padding: 20px 30px;
  text-decoration: none;


  background-color: rgba(0, 0, 0, 0);
  color: rgb(214, 214, 214);
  border: none;
  padding-right: 30%;
  transition: 0.2s all;
}

@media (max-width: 1000px) {

  .buttonsMenu {
    height: fit-content;
    justify-items: center;
    justify-content: center;
    align-items: end;
    display: grid;
    grid-template-columns:repeat( 5, 19% );
    height: 100%;
  }

  .linkActive {
    padding: 5px 5px;
    border: none;
    border-bottom: 3px solid white;
    width: 100%;
    display: flex;
    justify-content: center;

    transition: 0.1s all;
  }

  .linkNotActive {
    padding: 5px 5px;
    border: none;
    border-bottom: 0px solid white;
    width: 100%;
    display: flex;
    justify-content: center;

    transition: 0.1s all;
  }

  .linkActive img {
    width: 45px;
    margin-bottom: 5px;

    transition: all 0.1s;

  }

  .linkNotActive img {
    width: 37px;
    margin-bottom: 12px;
    transition: all 0.1s;
  }
}

/*-----Geosearch----------------------------------------------*/

.geosearchCard {
  display: grid;
  grid-template-columns: 1fr;
  -ms-flex-line-pack: start;
  align-content: start;
  width: 80%;
  margin: auto;
  margin-top: 5%;
  background-color: rgba(0, 0, 0, 0.432);
  -webkit-backdrop-filter: blur(8px);
  backdrop-filter: blur(8px);
  border-radius: 10px;
  padding: 3% 5%;
  padding-top: 2%;
}
.geosearchCard h1{
  color: white;
  text-align: center;
  margin-bottom: 2%;
}
.geosearchCard input{
  width: 80%;
  margin: auto;
  padding: 10px 6px;
  font-size: 22px;
  font-weight: 500;
  color: rgb(255, 255, 255);
  text-align: center;
  border-radius: 4px;
  background-color: rgba(255, 255, 255, 0.349);
}
.geosearchCard input::-webkit-input-placeholder {
  color: rgb(224, 224, 224);
  font-size: 20px;
}
.geosearchCard input:-moz-placeholder {
  color: rgb(224, 224, 224);
  font-size: 20px;
}
.geosearchCard h3{
  width: fit-content;
  color: rgb(192, 192, 192);
  font-size: 17px;
  text-align: center;
  margin: auto;
  font-weight: 400;
  padding: 0 30px;
}

.geosearchCard ul{
  list-style: none;
  padding-left: 0;
}
.geosearchCard ul li{
  width: 93%;
  border-bottom: 1px solid black;
  margin: 10px;
  transition: all 0.2s;
}
.geosearchCard ul li .link {
  width: 94%;
  display: grid;
  grid-template-columns: 1fr 10%;
  justify-items: stretch;
  align-items: center;
  color: white;
  font-size: 21px;
  text-align: center;
  padding: 15px;
  text-decoration: none;
}

.geosearchCard ul li:hover{
  width: 70%;
  border-bottom: 1px solid rgb(255, 255, 255);

  transition: all 0.2s;
}


/*------Now---------------------------------------------------*/
.nowCard {
  display: grid;
  grid-template-columns: 1fr;
  -ms-flex-line-pack: start;
  align-content: start;
  width: 80%;
  margin: auto;
  background-color: rgba(0, 0, 0, 0.432);
  -webkit-backdrop-filter: blur(8px);
  backdrop-filter: blur(8px);
  border-radius: 10px;
  padding: 3%;
  padding-top: 2%;
}

.headerNow {
  color: white;
  text-align: center;
  margin-bottom: 2%;
}

.headerNow h1 {
  margin: 0;
  margin-top: 10px;
}

.dateInfo h3 {
  color: white;
  margin: 0;
  margin-left: 23.5%;
}

.mainNow {
  display: grid;
  grid-template-columns: 40% 40%;
  -webkit-box-pack: space-evenly;
  -ms-flex-pack: space-evenly;
  justify-content: space-evenly;
}

.mainNow img {
  width: 90%;
  margin-top: 3.5%;
  margin-left: 5%;
}

.bottomImgInfo {
  background-color: rgba(151, 151, 151, 0.308);
  width: 100%;
  border-radius: 10px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  color: white;
  text-align: center;
}

.additionalInformation {
  align-self: start;
  display: grid;
  grid-template-columns: 1fr;
  gap: 3%;
}

.additionalHeader {
  background-color: rgba(163, 163, 163, 0.308);
  text-align: center;
  border-radius: 8px;
  width: 100%;
  margin-bottom: 2%;
}

.additionalHeader h3 {
  font-weight: 400;
  color: white;
}

.additionalContainerInfo {
  display: grid;
  grid-template-columns: 1fr;
  gap: 4%;
}

.blockInfo {
  margin-left: 8%;
  display: grid;
  grid-template-columns: 23% 1fr;
  color: white;
}

.blockInfo img {
  justify-self: end;
  -ms-flex-item-align: center;
  align-self: center;
  width: 70%;
}

.blockInfoText {
  margin-top: 2%;
  margin-left: 12%;
}

.blockInfo h3,
.blockInfo h4 {
  margin: 4%;
}

.lightNow {
  font-weight: 300;
}

.boldNow {
  font-weight: 600;
}


/*----Today and Tomorrow--------------------------------------*/

.todayCard {
  display: grid;
  width: 80%;
  grid-template-columns: 1fr;
  -ms-flex-line-pack: start;
  align-content: start;
  margin: auto;
  background-color: rgba(0, 0, 0, 0.432);
  -webkit-backdrop-filter: blur(8px);
  backdrop-filter: blur(8px);
  border-radius: 10px;
  padding: 5%;
  padding-top: 2%;
}

.todayInfoTop {
  margin: 0;
}

.todayInfoTop h1 {
  color: white;
  margin: 0;
  margin-bottom: 2%;
  text-align: center;
}

.byHours {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  row-gap: 1.2%;
  -webkit-column-gap: 3%;
  -moz-column-gap: 3%;
  column-gap: 3%;
  width: 100%;
  justify-items: center;

}

.hourCard {
  display: grid;
  grid-template-columns: 1fr;
  border: 1px solid black hidden;
  width: 100%;
  padding: 7% 12%;
  background-color: rgba(0, 0, 0, 0.171);
  border-radius: 7px;
}

.rightNowToday {
  display: grid;
  grid-template-columns: 1fr;
  border: 1px solid black hidden;
  width: 100%;
  padding: 7% 12%;
  background-color: rgba(0, 0, 0, 0.308);
  border-radius: 7px;
}

.hourTodayBlock {
  text-align: center;
  color: white;
  font-weight: 600;
  margin-bottom: 3%;
}

.conditionTodayBlock {
  background-color: rgba(163, 163, 163, 0.308);
  text-align: center;
  padding: 3% 10% 5% 10%;
  border-radius: 8px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: auto;
  color: white;
  font-weight: 300;
  margin-bottom: 3%;
}

.imageTodayBlock img {
  width: 70%;
  padding-left: 13%;
}

.bottomInfoTodayBlock {
  justify-self: center;
  background-color: rgba(163, 163, 163, 0.308);
  text-align: center;
  padding: 5% 5%;
  border-radius: 8px;
  width: 95%;
  color: white;
  font-weight: 300;
  font-size: 15px;
}

.bottomInfoTodayBlock span {
  font-weight: 500;

}



/*-------Week-------------------------------------------------*/

.weekCard {
  display: grid;
  width: 75%;
  grid-template-columns: 1fr;
  -ms-flex-line-pack: start;
  align-content: start;
  margin: auto;
  background-color: rgba(0, 0, 0, 0.432);
  -webkit-backdrop-filter: blur(8px);
  backdrop-filter: blur(8px);
  border-radius: 10px;
  padding: 5%;
  padding-top: 2%;
}

.weekInfoTop {
  margin: 0;
  padding: 0;
}

.weekInfoTop h1 {
  color: white;
  margin: 0;
  margin-bottom: 2.7%;
  text-align: center;
}

.daysWeekBlock {
  display: grid;
  width: 100%;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  justify-items: center;
  -webkit-column-gap: 4%;
  -moz-column-gap: 4%;
  column-gap: 4%;
  row-gap: 2%;
}

.dayCard {
  display: grid;
  grid-template-columns: 1fr;
  border: 1px solid black hidden;
  width: 90%;
  padding: 7% 12%;
  background-color: rgba(0, 0, 0, 0.171);
  border-radius: 7px;
}

.rightNowWeek {
  display: grid;
  grid-template-columns: 1fr;
  border: 1px solid black hidden;
  width: 90%;
  padding: 7% 12%;
  background-color: rgba(0, 0, 0, 0.308);
  border-radius: 7px;
}

.yesterdayWeek {
  grid-template-rows: 40% 45% 15%;
}


.headerWeekDayCard {
  justify-self: center;
  color: white;
  font-weight: 600;
  margin-bottom: 3%;
}

.conditionWeekDayCard {
  background-color: rgba(163, 163, 163, 0.308);
  justify-self: center;
  padding: 3% 10% 4% 10%;
  border-radius: 8px;
  width: fit-content;
  color: white;
  font-weight: 300;
  margin-bottom: 3%;
}

.imageWeekDayCard img {
  width: 50%;
  margin-left: 25%;
}

.bottomInfoWeekDayCard {
  background-color: rgba(163, 163, 163, 0.308);
  justify-self: center;
  text-align: center;
  padding: 2% 5%;
  border-radius: 8px;
  width: 95%;
  color: white;
  font-size: 15px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
}

.bottomInfoWeekDayCard h4 {
  margin: 7% 0;
  padding: 0;
  font-weight: 300;
}

.bottomInfoWeekDayCard h4 span {
  margin: 0;
  padding: 0;
  font-weight: 600;
}




/*-----loader*/

.loader {
  margin: auto;
  border: 7px solid #00000000;
  border-top: 7px solid rgba(0, 0, 0, 0.432);
  border-radius: 50%;
  width: 120px;
  height: 120px;
  -webkit-animation: spin 0.5s linear infinite;
  animation: spin 0.5s linear infinite;
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

